@use "./colors" as *;

@mixin standartBlock($direction:column, $justify: space-between, $align: center, $width:100%){
  position: relative;
  display: flex;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
  width: $width;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

@mixin standartTitle($size:2em, $weight:600, $color:$titleColor){
  width: 100%;
  font-size: $size;
  font-weight: $weight;
  color: $color;
  text-align: center;
}

@mixin scoreTitle () {
  color: $scoreTitle;
  text-shadow: 0px 0px 5px #4744EB;
}

@mixin scoreCount () {
  color: $navColor;
  text-shadow: 0px 0px 5px #f3f3fa;
}